/* sections */

.intro {
  background-image: url("../images/intro-bg.png");
}

.container--intro {
  justify-content: space-between;
}

.header {
  position: relative;
  padding-top: 12px;
  display: flex;
  align-items: center;
  width: 100%;
}

.logo--intro {
  margin-right: 15px;
}

.header__nav {
  margin-left: auto;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

.header__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0;
  text-align: right;
}

.header__list-item {
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
}

.header__list-item::after {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: #ffffff;
  right: -2px;
  bottom: 9px;
}

.header__list-item:last-child::after {
  display: none;
}

.header__link {
  display: inline-block;
  text-decoration: none;
  color: #ffffff;
  padding: 5px 15px;
  position: relative;
}

.intro__visual {
  display: flex;
  margin: 0;
}

.header__hambmenu-btn {
  width: 24px;
  height: 22px;
  cursor: pointer;
  display: block;
  background-color: transparent;
  margin: 0;
  padding: 0;
  margin-left: 30px;
  border: none;
  position: relative;
  display: none;
}

.header__hambmenu-btn-lines {
  width: 24px;
  height: 4px;
  position: absolute;
  top: 0;
  cursor: pointer;
  display: block;
  margin: 0;
  padding: 0;
  border: none;
  background-color: #ffffff;
  border-radius: 100px;
  box-shadow: 0 9px 0 0 #ffffff, 0 18px 0 0 #ffffff;
}

.intro__picture {
  width: 451px;
  height: 424px;
  margin-right: 10px;
}

.intro__title {
  color: #f9b43b;
  font-family: "Gagalin", sans-serif;
  font-size: 72px;
  font-weight: 400;
  line-height: 85px;
  margin: 0;
  margin-top: 130px;
}

.intro__title--lettering {
  display: block;
  font-size: 65px;
  color: #ffffff;
  line-height: 85px;
  font-family: "Lifehack";
  font-weight: 500;
}

.intro__control-down {
  font-size: 0px;
  display: block;
  width: 30px;
  height: 20px;
  position: relative;
  margin: 0 auto;
  margin-top: 25px;
  margin-bottom: 25px;
}

.intro__control-down svg {
  width: 30px;
  height: 20px;
  fill: #ffffff;
  transition-property: all;
  transition-duration: 0.5s;
}

.intro__control-down svg:hover {
  fill: #f9b43b;
}

.hambmenu {
  background-color: #2f3234;
  align-items: flex-start;
  overflow: hidden;
  z-index: 10000;
  display: none;
  opacity: 0;
  transition-property: all;
  transition-duration: 0.6s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.container--hambmenu {
  min-width: 708px;
}

.hambmenu__header {
  padding-top: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: auto;
}

.hambmenu__close-btn {
  position: relative;
  cursor: pointer;
  display: block;
  background-color: transparent;
  width: 18px;
  height: 18px;
  margin: 0;
  padding: 0;
  border: none;
  text-align: center;
  color: #ffffff;
}

.hambmenu__close-btn::before {
  position: absolute;
  content: "";
  display: block;
  background-color: #c1c2c2;
  width: 23px;
  height: 3px;
  border-radius: 10px;
  transform: rotate(45deg);
}

.hambmenu__close-btn::after {
  position: absolute;
  content: "";
  display: block;
  background-color: #c1c2c2;
  width: 23px;
  height: 3px;
  border-radius: 10px;
  transform: rotate(-45deg);
}

.hambmenu__nav {
  margin-top: 180px;
}

.hambmenu__list {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 24px;
  line-height: 1;
  font-family: "Ubuntu", sans-serif;
  font-weight: normal;
}

.hambmenu__list-item {
  padding: 0;
  margin: 0;
  text-align: center;
  padding-bottom: 30px;
  margin-bottom: 23px;
  position: relative;
}

.hambmenu__list-item::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 7px;
  height: 7px;
  border-radius: 100%;
  background-color: #c1c2c2;
}

.hambmenu__list-item:last-child {
  padding: 0;
  margin: 0;
}

.hambmenu__list-item:last-child::after {
  display: none;
}

.hambmenu__link {
  color: #c1c2c2;
  text-decoration: none;
}

.features {
