  height: 100%;
  min-height: 650px;
  width: 100%;
}

.reviews__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
}

.reviews__item {
  flex-basis: 25%;
  height: 50%;
  position: relative;
  flex-grow: 1;
}

.reviews__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(20, 20, 20, 0.7);
  opacity: 0;
  transition-property: all;
  transition-duration: 0.3s;
}

.reviews__item:hover::after {
  opacity: 1;
}

.reviews__avatar {
  display: block;
  width: 100%;
  height: 50%;
  object-fit: cover;
  filter: grayscale(100%);
}

.reviews__preview {
  opacity: 0;
  z-index: 10;
  width: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition-property: all;
  transition-duration: 0.3s;
}

.reviews__item:hover > .reviews__preview {
  opacity: 1;
}

.reviews__autor-name {
  color: #ffffff;
  font-family: "Gagalin", sans-serif;
  font-size: 14px;
  margin: 0;
  font-weight: 400;
  line-height: 1.2;
}

.reviews__info {
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}

.buy-button--more {
  background-color: #2f3234;
}

.buy-button--more:hover {
  background-color: #1f2122;
}

.buy {
  background-image: url("../images/buy-bg.jpg");
}

.buy__title {
  color: #f9b43b;
  font-family: "Gagalin", sans-serif;
  font-size: 72px;
  font-weight: 400;
  line-height: 1.2;
  margin: 0;
  margin-bottom: 40px;
  text-align: center;
}

.buy__forms {
  display: flex;
  justify-content: space-between;
}

.buy__left-forms {
  flex-basis: 50%;
  flex-grow: 1;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 205px;
}

.buy__right-forms {
  flex-basis: 50%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 260px;
}

.buy__form-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}

.buy__label {
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  display: block;
  margin-bottom: 7px;
}

.buy__input {
  display: block;
  background-color: #ffffff;
  border: 1px solid #d1cfcb;
  border-radius: 5px;
  height: 35px;
  padding: 10px;
  outline: none;
  box-sizing: border-box;
  width: 100%;
}

.buy__input:focus {
  border: 2px solid #f9b43b;
}

.buy__input--textarea {
  height: 110px;
}

.buy__form-element {
  position: relative;
}

.buy__left-forms .buy__form-line:nth-child(1) .buy__form-element {
  flex-basis: 50%;
  margin-right: 20px;
}

.buy__left-forms .buy__form-line:nth-child(1) .buy__form-element:last-child {
  margin-right: 0;
}

.buy__left-forms .buy__form-line:nth-child(2) .buy__form-element {
  flex-basis: 100%;
}

.buy__left-forms .buy__form-line:nth-child(3) .buy__form-element {
  flex-basis: 25%;
  margin-right: 20px;
}

.buy__left-forms .buy__form-line:nth-child(3) .buy__form-element:last-child {
  margin-right: 0;
}

.buy__right-forms .buy__form-line:nth-child(1) .buy__form-element {
  flex-basis: 100%;
}

.buy__radio {
  display: none;
}

.buy__label-radio {
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  cursor: pointer;
  margin-right: 25px;
}

.buy__label-radio:last-child {
  margin-right: 0;
}

.buy__label-radio::before {
  content: "";
  display: inline-block;
  width: 7px;
  height: 7px;
  background-color: #ffffff;
  border-radius: 100%;
  border: 4px solid #ffffff;
  position: relative;
  top: 3px;
  margin-right: 10px;
}

.buy__radio:checked + .buy__label-radio::before {
  background-color: #e35028;
}

.buy__checkbox {
  display: none;
}

.buy__label-checkbox {
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  cursor: pointer;
}

.buy__label-checkbox::before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  background-color: #ffffff;
  border-radius: 1px;
  position: relative;
  top: 3px;
  margin-right: 10px;
}

.buy__checkbox:checked + .buy__label-checkbox::before {
  background-image: url("../images/checkbox.png");
  background-size: 11px 9px;
  background-position: center center;
  background-repeat: no-repeat;
}

.buy-button--margin-right {
  margin-right: 17px;
}

.buy__reset {
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  background-color: transparent;
  border: none;
}

.buy__validate-message {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(120%);
  font-size: 16px;
  color: #d1cfcb;
  background-color: #e35028;
  border-radius: 5px;
  width: 100%;
  line-height: 1.5;
}

.buy-modal {
  background-color: rgba(47, 50, 52, 0.9);
  width: 100%;
  height: 100%;
  z-index: 10000;
  transition-property: all;
  transition-duration: 0.6s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition-property: all;
  transition-duration: 0.6s;
}

.buy-modal__container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  border-radius: 5px;
  min-width: 160px;
  min-height: 66px;
  padding: 20px;
}

.buy-modal__text {
  display: block;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 14px;
  color: #2f3234;
  line-height: 1.2;
  text-align: center;
}

.footer {
  position: relative;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.footer__map {
  width: 100%;
  flex-grow: 1;
}

.footer__map-desc {
  color: #e45028;
  font-family: "Ubuntu", sans-serif;
  font-size: 18px;
  font-weight: 300;
  font-style: normal;
  line-height: 1.5;
}

.footer__main-footer {
  background-color: #121212;
  bottom: 0;
  width: 100%;
}

.container--footer {
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.footer__flex-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__contacts {
  flex-basis: 30%;
}

.footer__address {
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
}

.footer__phone {
  color: #e45028;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-decoration: none;
  padding-left: 20px;
  position: relative;
}

.footer__phone::before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 11px;
  background-image: url("../images/icons/phone.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  left: 0;
  top: 3px;
}

.footer__social {
  flex-basis: 30%;
  text-align: right;
}

.footer__social-link {
  font-size: 0px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 20px;
}

.footer__social-link:hover .footer__social-icon {
  opacity: 1;
}

.footer__social-link:first-child .footer__social-icon {
  width: 23px;
  height: 13px;
}

.footer__social-link:last-child {
  margin-right: 0;
}

.footer__social-icon {
  width: 20px;
  height: 20px;
  opacity: 0.3;
  transition-property: all;
  transition-duration: 0.5s;
}

.review-popup {
  background-color: rgba(47, 50, 52, 0.9);
  width: 100%;
  height: 100%;
  z-index: 10000;
  transition-property: all;
  transition-duration: 0.6s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition-property: all;
  transition-duration: 0.6s;
}

.review-popup__container {
  padding: 30px 20px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  border-radius: 5px;
  width: 420px;
  height: auto;
}

.review-popup__name {
  margin: 0;
  font-family: "Gagalin", sans-serif;
  font-size: 14px;
  color: #2f3234;
  line-height: 1.2;
  margin-bottom: 25px;
}

.review-popup__text {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  color: #2f3234;
  line-height: 1.7;
}

.review-popup__close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  display: block;
  background-color: transparent;
  width: 18px;
  height: 18px;
  margin: 0;
  padding: 0;
  border: none;
  text-align: center;
  color: #ec8468;
}

.review-popup__close-btn::before {
  position: absolute;
  content: "";
  display: block;
  background-color: #ec8468;
  width: 23px;
  height: 3px;
  border-radius: 10px;
  transform: rotate(45deg);
}

.review-popup__close-btn::after {
  position: absolute;
  content: "";
  display: block;
  background-color: #ec8468;
  width: 23px;
  height: 3px;
  border-radius: 10px;
  transform: rotate(-45deg);
}

@media (max-width: 768px) {
  .wrapper {
    min-width: 768px;
  }

  .container {
    max-width: 708px;
    padding: 0 4%;
  }

  .header__list {
    display: none;
  }

  .intro__visual {
    flex-direction: column;
    align-items: center;
    width: 75%;
  }

  .header__hambmenu-btn {
    display: block;
  }

  .intro__picture {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .intro__title {
    text-align: center;
    margin-top: 0;
  }

  .container--products-section {
    flex-direction: column;
  }

  .products__slider {
    width: 328px;
  }

  .products__slider-item {
    flex-direction: column;
  }

  .products__image-container {
    margin-right: 0;
    width: 75%;
  }

  .products__image {
    width: 100%;
  }

  .products__description {
    align-items: center;
    text-align: center;
    width: 75%;
  }

  .products__ingridients-button {
    left: 30px;
  }

  .products__ingridients-list {
    left: 120px;
  }

  .products__kkal {
    bottom: auto;
    left: auto;
    top: 0;
    right: 30px;
  }

  .team__content {
    margin-left: 0;
    width: 100%;
  }

  .team__content-wrapper {
    align-items: center;
  }

  .team__list {
    align-items: center;
  }

  .team__name {
    text-align: center;
  }

  .team__people-decription {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .team__name--active + .team__people-decription {
    height: 237px;
  }

  .team__avatar {
    margin-bottom: 15px;
  }

  .team__info {
    text-align: center;
  }

  .menu__content {
    max-width: 100%;
  }

  .menu__cover--active + .menu__description {
    width: 530px;
  }

  .reviews__item {
    flex-basis: 50%;
    height: 25%;
    text-align: center;
  }

  .reviews__avatar {
    height: 25%;
  }

  .buy__forms {
    flex-direction: column;
    justify-content: flex-start;
    width: 58.5%;
  }

  .buy__left-forms {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .footer__contacts {
    flex-basis: 50%;
  }
}

@media (max-width: 480px) {
  .wrapper {
    min-width: 480px;
  }

  .container {
    max-width: 440px;
    padding: 0 4.5%;
  }

  .container--hambmenu {
    min-width: 440px;
  }

  .hambmenu__nav {
    margin-top: 100px;
  }

  .features__list {
    flex-direction: column;
    justify-content: flex-start;
    text-align: left;
  }

  .features__list-item:nth-child(1) {
    background-position: left top;
  }

  .features__list-item:nth-child(2) {
    background-position: left top;
  }

  .features__list-item:nth-child(3) {
    background-position: left top;
    margin-bottom: 0;
  }

  .features__list-item {
    margin-right: 0;
    flex-basis: auto;
    padding-top: 0;
    padding-left: 120px;
    margin-bottom: 40px;
  }

  .products__description {
    width: 100%;
  }

  .products__control--left {
    left: -10px;
  }

  .products__control--right {
    right: -10px;
  }

  .menu__cover--active + .menu__description {
    width: 240px;
  }

  .reviews__preview {
    text-align: center;
  }

  .reviews__autor-name {
    font-size: 24px;
  }

  .reviews__info {
    display: none;
  }

  .buy-button--more {
    margin-top: 25px;
  }

  .buy__title {
    font-size: 50px;
  }

  .buy__forms {
    width: 100%;
  }

  .buy__left-forms .buy__form-line:nth-child(3) .buy__form-element {
    flex-basis: 33%;
  }

  .buy__left-forms .buy__form-line:nth-child(3) .buy__form-element:nth-child(4) {
    display: none;
  }

  .buy__left-forms .buy__form-line:nth-child(3) .buy__form-element:nth-child(3) {
    margin-right: 0;
  }

  .buy__right-forms .buy__form-line:nth-child(2) .buy__form-element {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .buy__right-forms .buy__form-line:nth-child(3) .buy__form-element {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .buy__right-forms .buy__form-line:nth-child(4) .buy__form-element {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .footer__flex-wrapper {
    flex-direction: column;
    justify-content: flex-start;
    padding: 20px 0;
  }

  .footer__contacts {
    text-align: center;
    margin-bottom: 15px;
  }
}