@font-face {
  font-family: 'Lifehack';
  src: url('../fonts/Lifehack-Medium.eot');
  src: url('../fonts/Lifehack-Medium.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Lifehack-Medium.woff2') format('woff2'),
      url('../fonts/Lifehack-Medium.woff') format('woff'),
      url('../fonts/Lifehack-Medium.ttf') format('truetype'),
      url('../fonts/Lifehack-Medium.svg#Lifehack-Medium') format('svg');
  font-weight: 500;
  font-style: normal;
}

